
import { reactive } from 'vue';
import { Input } from '../../../src';
export default {
  name: 'demo-input',
  setup() {
    const inputRef = Input.use.ref('myInput');
    const state = reactive({
      text: 'hello',
    });
    const handler = {
      clear: () => {
        inputRef.value.methods.clear();
      },
    };
    return {
      ...handler,
      state,
    };
  },
};
